import {LogMessageType, encrypt} from "./ff14";
import streamSaver from "streamsaver";

const fileSelector = document.getElementById('file-selector');
const progress_bar = document.getElementById("parseBar");
const bar = document.getElementById("parseProgress");
const download_button = document.getElementById("downloadFile");

function clear_screen()
{
    clear_progress_bar();

    if (download_button == null) return;
    download_button.style.display = 'none';
}

function clear_progress_bar()
{
    if (bar == null) return;
    bar.style.display = "none";
}

function create_progress_bar()
{   
    if (bar == null) return;
    bar.style.display = "block";
}

// remove white space from the beginning and end of the string
function Uint8Array_trim(arr: Uint8Array): Uint8Array
{
    let start = 0;
    let end = arr.length;
    while (start < end && arr[start] == 32)
        start++;
    while (end > start && arr[end - 1] == 32)
        end--;
    return arr.slice(start, end);
}

function set_progress_bar(percentage: number) 
{   
    if (percentage > 1 || percentage < 0) return;

    if (progress_bar == null || bar == null) return;

    progress_bar.style.width = percentage * 100 + "%";
    progress_bar.innerHTML = percentage * 100 + "%";
}

function read_file(file: Blob) {
    const fileStream = streamSaver.createWriteStream((<any>file).name)
    const readableStream = file.stream();
    const file_size = file.size;
    let size_counter = 0;

    const writer = fileStream.getWriter();

    const reader = readableStream.getReader();
    let offset = 0;
    let unfinished_chunk = Uint8Array.from([]);
    let counter = 0;

    const pump = () => { 
        let to_write = Uint8Array.from([]);
        offset = 0;

        reader.read()
        .then(res => {
            if (res.done) {
                writer.close();
                return;
            }

            size_counter += res.value.length;
            set_progress_bar(size_counter / file_size);

            while (offset <= res.value.length) {
                const next_line = res.value.indexOf(10, offset);
                if (next_line == -1) {
                    unfinished_chunk = new Uint8Array([...unfinished_chunk, ...res.value.slice(offset, res.value.length)]);
                    break;
                }
                if(unfinished_chunk.length > 0) {
                    to_write = new Uint8Array([...unfinished_chunk, ...res.value.slice(offset, next_line)]);
                    unfinished_chunk = Uint8Array.from([]);
                }
                else {
                    to_write = res.value.slice(offset, next_line);
                }
                
                // --------- parse the line ------------
                let line = Uint8Array_trim(to_write);
                if (line.length <= 0) continue;
            
                let loc1 = line.indexOf(124);
                let loc2 = line.lastIndexOf(124);
                
                const decoder = new TextDecoder();
                let message_type = Number(decoder.decode(line.slice(0, loc1)));
                let text = line.slice(0, loc2);
                
                if (message_type == LogMessageType.Version || message_type == LogMessageType.Territory)
                    counter = 0;
                
                ++counter;
        
                const encoder = new TextEncoder();
                const encrypted_text = encrypt(new Uint8Array([...text, 124, ...encoder.encode(counter.toString())]));
                
                writer.write(new Uint8Array([...text, 124, ...encrypted_text, 10]));

                offset = next_line + 1;
                
            }
            return writer.ready.then(pump);
        })
    }

    pump(); 
}

function open_file(event: any) 
{   
    create_progress_bar();
    read_file(event.target.files[0]);
}

function main()
{   
    if (fileSelector == null) 
    {
        console.log("fileSelector is null");
        return;
    }
    fileSelector.addEventListener('change', (event) => {
        open_file(event);
    });

    if (download_button == null) 
    {
        console.log("download_button is null");
        return;
    }
    download_button.addEventListener("click", clear_screen);
}

main()